
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.top-enter{
  opacity: 0;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.top-enter-to{
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.top-enter-active,
.top-leave-active{
  -webkit-transition:all 0.7s;
  transition:all 0.7s;
}
.top-leave{
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.top-leave-to{
  opacity: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.bottom-enter{
  opacity: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.bottom-enter-to{
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.bottom-enter-active,
.bottom-leave-active{
  -webkit-transition:all 0.7s;
  transition:all 0.7s;
}
.bottom-leave{
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.bottom-leave-to{
  opacity: 0;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}



