html, body {
  margin: 0;
  padding: 0;
  background-color: #f2f2f2; }

ul, li {
  list-style: none; }

h2 {
  margin: 0;
  padding: 0; }

ul {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

::-webkit-scrollbar {
  width: 0px; }

.warp {
  width: 100%;
  height: 100%;
  background-color: #a3be8c;
  padding-top: 75px; }

.main {
  width: 96%;
  margin: 0px auto;
  position: relative;
  padding: 5px;
  background-color: #fff;
  border: none;
  -webkit-box-shadow: -2px -2px 5px rgba(153, 153, 153, 0.5);
          box-shadow: -2px -2px 5px rgba(153, 153, 153, 0.5); }

.nav-left {
  width: 20%;
  border: 1px solid transparent;
  border-right-color: #dddddd;
  text-align: center; }

.nav-left h2 {
    margin: 80px auto 60px;
    text-align: center; }

.nav-left hr:first-of-type,
  .nav-left hr:last-of-type {
    border: none;
    height: 1px;
    width: 150px;
    background: #dddddd; }

.nav-left ul {
    padding: 25px 0 25px; }

.nav-left ul li {
      text-align: center;
      margin-bottom: 20px; }

.nav-left ul li a {
        color: #2c3e50;
        font-size: 18px; }

.nav-left div {
    width: 100%;
    height: 80px;
    line-height: 80px; }

.nav-left div a {
      padding: 5px;
      margin: 5px; }

.show-main {
  width: 78%;
  height: 90%;
  border: 1px solid transparent;
  position: absolute;
  top: 5px;
  right: 5px; }

.footer {
  width: 200px;
  text-align: center;
  position: absolute;
  bottom: 10px;
  left: 680px; }

.footer a {
    color: #2c3e50;
    text-decoration: underline; }

.center {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0; }
